import axios from 'axios'
// 王志丹:0321c6d7-8475-441b-8303-a9f5581bfd76
// no-patient:48f6f4db-ea5b-4553-a733-0c870f1a06d7
// 70+doctors: 34725857-da66-4e24-937a-3dc3bf2a9a81

const httpClient = axios.create({
  // baseURL: 'http://localhost:29832',
  // baseURL: 'http://newlccp.vm-online.com/lccp/',
  timeout: 0,
  // headers: { 'Cache-Control': 'no-cache, must-revalidate', 'Dev-Token': '0321c6d7-8475-441b-8303-a9f5581bfd76' }
  headers: { 'Cache-Control': 'no-cache, must-revalidate' }
  // withCredentials: true
})

// 拦截响应response，并做一些错误处理
httpClient.interceptors.response.use((response) => {
  return response.data
}, (err) => {
  if (err && err.response) {
    console.log(err.response.status)
    window.location.href = '/lccp?router=no-permission'
  }
  return Promise.reject(err)
})

// httpClient.install = (Vue) => {
//   Vue.prototype.$httpClient = httpClient
// }

export default httpClient



