import httpClient from '../common/httpClient'

// initial state
const state = {
  pageInfo: {
    pageName: '',
    pageId: '',
    requestURL: ''
  },
  intervalId: -1
}

// mutations
const mutations = {
  updatePageId (state, data) {
    state.pageInfo = data
  },
  setTrackingIntervalId (state, data) {
    state.intervalId = data
  },
  clearTrackingInterval (state) {
    clearInterval(state.intervalId)
    state.pageInfo = {
      pageName: '',
      pageId: '',
      requestURL: ''
    }
  }
}

// getters
const getters = {
  pageInfo: state => state.pageInfo,
  intervalId: state => state.intervalId
}

// actions
const actions = {
  getPageId ({ commit }, pageInfo) {
    httpClient.post('/lccp/TrackingLog/VuePageTracking', {
      pageName: pageInfo.pageName, requestURL: pageInfo.requestURL
    }).then(response => {
      if (response.returnCode === 200) {
        commit('updatePageId', response.data)
      }
    }).catch(function (error) {
      console.log(error)
    })
  },
  pageDuration ({ commit }) {
    if (state.pageInfo.pageId !== '') {
      httpClient.post('/lccp/TrackingLog/VuePageDuration', state.pageInfo).then(response => {
        if (response.returnCode !== 200) {
          commit('clearTrackingInterval')
        }
      }).catch(function (error) {
        console.log('pageDuration: ' + error)
        commit('clearTrackingInterval')
      })
    }
  },
  setTrackingIntervalId ({ commit }, intervalId) {
    commit('setTrackingIntervalId', intervalId)
  },
  clearTrackingInterval ({ commit }) {
    commit('clearTrackingInterval')
  },
  pageClickTracking ({commit}, data) {
    var pageInfo = state.pageInfo
    httpClient.post('/lccp/TrackingLog/VuePageClick', {
      pageName: pageInfo.pageName,
      pageId: pageInfo.pageId,
      requestURL: pageInfo.requestURL,
      clickMessage: data.message,
      logType: data.logType
    }).catch(function (error) {
      console.log('pageClickTracking: ' + error)
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



