import httpClient from '../common/httpClient'

// initial state
const state = {
  user: {},
  all: []
}

// getters
const getters = {
  user: state => state.user,
  username: state => state.user.FamilyName + state.user.GivenName,
  userDoctorId: state => state.user.DctrId ? state.user.DctrId : ''
}

// mutations
const mutations = {
  login (state, user) {
    const date = user.EnrollDate.slice(6, user.EnrollDate.length - 2)
    user.EnrollDays = (new Date().getTime() - parseInt(date)) / (24 * 60 * 60 * 1000)
    state.user = user
  },
  updateUser (state) {
    state.user.IsPocketDoctorRegister = true
  }
}

// actions
const actions = {
  getUser ({ commit }, requestURL) {
    httpClient.get('/lccp/DoctorBasicInfo/GetDoctorBasicInfo/').then(response => {
      if (response.returnCode !== 200) {
        location.href = '/lccp/MyLccp/LCCPHomePage?router=' + encodeURIComponent(requestURL)
      }
      commit('login', response.data)
    })
  },
  setPocket ({commit}) {
    httpClient.post('/lccp/account/SetPocketDoctorRegister').then(() => {
      commit('updateUser')
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



