import Router from 'vue-router'
import store from '../store'
import HomeView from '@/components/Views/HomeView/HomeView'

Vue.use(Router)

const vueRouter = new Router({
  // base: '/lccp/',
  // mode: 'history',
  routes: [
    {
      path: '/patient-list/:id',
      name: 'patientManage',
      component: HomeView
    },
    {
      path: '/patient-share',
      name: 'patientShare',
      component: resolve => require(['@/components/Views/PatientDetail/PatientDetail'], resolve)
    },
    {
      path: '/patient-detail/:id',
      name: 'PatientDetail',
      component: resolve => require(['@/components/Views/PatientDetail/PatientDetail'], resolve)
    },
    {
      path: '/patient-detail/:hcpId/:id',
      name: 'DepartPatientDetail',
      component: resolve => require(['@/components/Views/PatientDetail/PatientDetail'], resolve)
    },
    {
      path: '/setting',
      name: 'Setting',
      component: resolve => require(['@/components/Views/Setting/Setting'], resolve)
    },
    {
      path: '/invitation',
      name: 'Invitation',
      component: resolve => require(['@/components/Views/Invitation/Invitation'], resolve)
    },
    {
      path: '/card-record',
      name: 'CardRecord',
      component: resolve => require(['@/components/Views/CardRecord/CardRecord'], resolve)
    },
    {
      path: '/summary/:tab',
      name: 'Summary',
      component: resolve => require(['@/components/Views/Summary/Summary'], resolve)
    },
    {
      path: '/sr-summary/:hospitalId',
      name: 'srSummary',
      component: resolve => require(['@/components/Views/Summary/Summary'], resolve)
    },
    {
      path: '/depart-patient-list/:id',
      name: 'DepartPatientList',
      component: resolve => require(['@/components/Views/DepartPatientList/DepartPatientList'], resolve)
    },
    {
      path: '/no-permission',
      name: 'Error',
      component: resolve => require(['@/components/Views/Error/Error'], resolve)
    },
    {
      path: '*',
      component: resolve => require(['@/components/Views/Error/Error'], resolve)
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

vueRouter.beforeEach((to, from, next) => {
  store.dispatch('clearTrackingInterval')
  var pageName = to.name === 'patientManage' ? 'PatientList_' + to.params.id : to.name
  var requestURL = to.path
  if (pageName) {
    store.dispatch('getPageId', {
      pageName: pageName, requestURL: requestURL
    })
    var trackingIntervalId = setInterval(function () { store.dispatch('pageDuration') }, 3000)
    store.dispatch('setTrackingIntervalId', trackingIntervalId)
  }
  if (to.name !== 'Summary') {
    next()
  } else {
    const interval = setInterval(function () {
      if (store.getters.username) {
        clearInterval(interval)
        next()
      }
    }, 50)
  }
})

export default vueRouter



