import httpClient from '../common/httpClient'

// initial state
const state = {
  cardsInfo: {
    // CardCount: 0,
    // PatientRegQrCode: {
    //   returnCode: '200',
    //   data: '',
    //   ExpiresIn: 180
    // },
    // IsSendMessage: true
  },
  // cardRecords: [{
  //   HeadUrl: '',
  //   PatientName: '刘建华',
  //   ShowStatus: '血糖仪已签收',
  //   Enrollment_Code: '100738590',
  //   Card_Status: null,
  //   Patient_Status: null,
  //   Pid: null,
  //   Sign_Time: null,
  //   Delivery_Time: null,
  //   Receiving_Time: null
  // }],
  cardRecords: [],
  remindResult: '已提醒您的专员，会尽快为您补充邀请函<br/>给您带来的不便，请您谅解'
}

// mutations
const mutations = {
  refreshCard (state, data) {
    state.cardsInfo = data
    console.log(JSON.stringify(state.cardsInfo))
  },
  clearCard (state) {
    state.cardsInfo = {}
  },
  getCardHistory (state, data) {
    state.cardRecords = data
    console.log(JSON.stringify(state.cardRecords))
  },
  remindSR (state, data) {
    state.cardsInfo.IsSendMessage = true
    if (!data) {
      state.remindResult = '补卡提醒发送失败，请稍后重试'
    }
  }
}

// getters
const getters = {
  cardsInfo: state => state.cardsInfo,
  cardRecords: state => state.cardRecords,
  remindResult: state => state.remindResult
}

// actions
const actions = {
  refreshCard ({ commit }) {
    httpClient.get('/lccp/DoctorQrCode/Refresh').then(response => {
      console.log(response.data)
      commit('refreshCard', response.data)
    })
  },
  clearCard ({ commit }) {
    commit('clearCard')
  },
  remindSR ({ commit }) {
    httpClient.get('/lccp/SendMessageToSales/SendMessageToSr').then(response => {
      commit('remindSR', response.data)
    })
  },
  getCardHistory ({ commit }) {
    httpClient.get('/lccp/CardHistoryStatus/GetCardHistoryStatus').then(response => {
      console.log(response.data)
      commit('getCardHistory', response.data)
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



