<template>
  <div class="confirm-page" v-if="showConfirm">
    <div class="confirm" v-if="!hasSend && !sendSuccess">
      <div class="confirm-img">
        <img :src="(sendInfo.text && sendInfo.text === '鼓励一下') ? likeImg : msgImg" alt="">
      </div>
      <p class="confirm-msg">即将向患者发送"{{sendInfo.text}}"信息</p>
      <p class="confirm-btns">
        <span class="btn-close" @click="closeModal">取消</span>
        <span class="btn-ensure"
          @click="hasClick ? '' : (patientIds && patientIds.length ? batchConfirmSend() : confirmSend(sendInfo.value))">确定</span>
      </p>
    </div>
    <div class="success" v-if="hasSend || sendSuccess">
      <img :src="(sendInfo.text && sendInfo.text === '鼓励一下') ? likeImg : msgImg" alt="">
      <div class="success-text">
        <p>辛苦了, 已给患者发送提醒</p>
        <p>谢谢您</p>
      </div>
    </div>
  </div>
</template>
<script>
import msgImg from '../../../assets/Confirm/message.png'
import likeImg from '../../../assets/Confirm/Like_pop.png'
export default {
  props: ['showConfirm', 'sendInfo', 'hasSend', 'patientId', 'closeModal', 'patientIds', 'text'],
  data () {
    return {
      msgImg: msgImg,
      likeImg: likeImg,
      sendSuccess: false,
      hasClick: false
    }
  },
  computed: {
    returnCode () {
      return this.$store.getters.sendMsgCode
    }
  },
  methods: {
    confirmSend (type) {
      const data = {
        type: type,
        patientId: this.patientId
      }
      this.$store.dispatch('sendMsg', data)
      this.hasClick = true
    },
    batchConfirmSend () {
      this.$store.dispatch('batchSendMsg', {patientIds: this.patientIds, from: this.$route.params.id})
      this.hasClick = true
    }
  },
  watch: {
    showConfirm: function (val, oldVal) {
      if (val && this.hasSend) {
        setTimeout(this.closeModal, 2000)
      }
      if (!val) {
        this.sendSuccess = false
        this.hasClick = false
      }
    },
    returnCode: function (val, oldVal) {
      if (val) {
        if (val === 200) {
          this.sendSuccess = true
          setTimeout(this.closeModal, 2000)
        } else {
          this.closeModal()
          this.$toast(`发送"${this.sendInfo.text}"消息失败，请稍后重新尝试`, 'center')
        }
      }
      this.$store.dispatch('resetSendMsgCode')
    }
  }
}
</script>
<style lang="scss">
  @import "./style.scss";

</style>



