<template>
  <div class="home-view">
    <div class="fill-bg"></div>
    <header v-if="news.length">
      <!-- <swiper :options="swiperOption" ref="mySwiper" class="news">
        <swiper-slide v-for="(newItem,index) in news" :key="index"
          @click="goToUrl(newItem.LinkUrl)">
          {{newItem.Content}}
        </swiper-slide>
      </swiper> -->
      <ul class="news" ref="news" :style="{transition: currentIndex > (news.length) ? 'none' : 'all ease-in .5s',
          transform: currentIndex > news.length ? 'transLateY(0px)' : `transLateY(${-40 * currentIndex}px)`}">
        <li v-for="(newItem,index) in news" :key="index"
          @click="goToUrl(newItem.LinkUrl)">
          {{newItem.Content}}
        </li>
        <li @click="goToUrl(news[0].LinkUrl)" v-if="news[0]" key="last">
          {{news[0] ? news[0].Content : ''}}
        </li>
      </ul>
    </header>
    <section class="content">
      <div class="filter-wrp" ref="filters" :style="{top: !news.length ? '0' : ''}">
        <ul class="clearfix"
          v-show="!showSearch">
          <li class="filter" :class="currentFilter === filter.rule?'filter-active':''"
            v-for="(filter,index) in filters" :key="index"
            @click="changeFilter(filter.rule)">
            <span>{{filter.text}}</span>
            <i class="red-tip" v-if="haveNewMessage && (filter.text === '对话记录')"></i>
          </li>
        </ul>
        <p class="filter-icon" :style="{top: news.length ? '' : '0px' }" v-show="!showSearch">
          <span @click="showtags"><icon name="search"></icon></span>
        </p>
        <p class="filter-text" :style="{top: news.length ? '' : '0px'}" v-show="showSearch">
          <span class="search-icon"><icon name="search" scale="1.1"></icon></span>
          <input type="text" placeholder="请输入患者姓名或关爱码" v-model="searchValue">
          <span class="search-close" @click="showtags">
            取消
          </span>
        </p>
      </div>
      <div class="clearfix filter-second-wrp" :style="{marginTop: news.length ? '90px' : '50px'}" v-show="(currentFilter === 'All' || currentFilter === 'noTest') && !showSearch && this.sortPatients.length">
        <div class="filter-second" v-show="currentFilter === 'All'">
          <p @click="handleShowFilters">
            {{currentFilterSecond}}
            <span><icon :name="showSecondFilter ? 'angle-up' : 'angle-down'"></icon></span>
          </p>
          <ul :style="{display: showSecondFilter ? 'block' : 'none'}">
            <li v-for="(secondFilter, index) in filters[3].filterSecond"
              :key="index" @click="changeFilter('', secondFilter)"
              :style="{color: secondFilter.text === currentFilterSecond ? '#000' : ''}">
              {{secondFilter.text}}
            </li>
          </ul>
        </div>
        <span class="tip-numbers" @click="showChooseBtn">
          {{showChoose ? '取消' : '批量提醒测量'}}
        </span>
      </div>
      <div class="patients-wrp"
        :style="{marginTop: showSearch ? (news.length ? '110px' : '70px') : (news.length
          ? (currentFilter === 'All' || currentFilter === 'noTest'
            ? '0' : '110px') : (currentFilter === 'All' || currentFilter === 'noTest'
              ? '0' : '70px'))}">
        <ul class="patients" :class="showChoose ? 'show-choose' : ''">
          <li class="patient clearfix" v-if="(currentFilter !== 'msgRecords') || showSearch" v-for="patient in sortPatients"
            @click="showChoose ? choosePatient(patient.PatientId) : goToDetail(patient)"
            :key="patient.PatientId">
            <div class="choose-btn"
              @click.stop="choosePatient(patient.PatientId)"
              :class="choosePatients.indexOf(patient.PatientId) >= 0 ? 'choose-btn-active' : ''"></div>
            <div class="clearfix patient-right">
              <div class="patient-info">
                <img :src="patient.photo" class="patient-photo" alt="">
                <p :style="{fontSize: patient.Name.length > 4 ? '15px' : '17px'}">{{patient.Name}}</p>
                <span>{{patient.sex}}</span>
                <span class="patient-age">{{patient.age}}岁</span>
              </div>
              <div class="patient-test">
                <p>
                  <span class="enroll-time">{{patient.enrollDays}}</span>
                  <span class="like-icon" :class="patient.IsStar?'like':'unlike'"
                    @click.stop="isChangeLike ? '' : handleLike(patient)">
                    <icon :name="patient.IsStar ? 'star' : 'star-o'" scale="1.3"></icon>
                  </span>
                </p>
                <p class="count-text" :style="{color: patient.IsNew ? '#9b9b9b' : ''}">
                  {{patient.IsNew
                    ? '患者未开始测量' : (patient.testCount
                      ? ((patient.lowCount ? '近7天测量' + patient.testCount + '次 低血糖' + patient.lowCount + '次' : '近7天测量' + patient.testCount + '次'))
                        : (patient.enrollBeforeWeek ? '血糖仪发放中' : '近7天无测量'))
                  }}
                </p>
                <p class="tags">
                  <span v-for="(tag,index) in patient.tags" :key="index" class="tag"
                  :style="{color: tag === '高血糖' ? '' : '#D73A2E'}">
                    <img
                    :src="tag !== '极低血糖' ? (tag === '高血糖' ? highIcon : low3Icon) : lowIcon" alt="">
                    {{tag}}
                  </span>
                </p>
              </div>
            </div>
          </li>
          <li class="patient msg-patient clearfix"
            v-if="(currentFilter === 'msgRecords') && !showSearch"
            v-for="patient in sortPatients"
            @click="goToPocket(patient)"
            :key="patient.PatientId">
            <div class="clearfix patient-right">
              <div class="patient-info">
                <img :src="patient.photo" class="patient-photo" alt="">
              </div>
              <div class="patient-test">
                <p class="count-text">
                  {{patient.Name}}
                </p>
                <p>
                  <span>{{patient.KouDai_MessageDate}}</span>
                  {{patient.KouDai_From === 'hcp' ? '您发送了消息' : '对方留言'}}
                </p>
              </div>
              <div v-if="!patient.KouDai_IsHcpRead && patient.KouDai_From === 'patient'" class="pocket-link">
                <img src="../../../assets/HomeView/new_msg.png">
              </div>
            </div>
          </li>
        </ul>
        <div class="function-link clearfix"
          v-show="user.EnrollDays < 28 && !showSearch && currentFilter === 'default'"
          @click="togglePop">
          <div class="link-pic">
            <img src="../../../assets/HomeView/yx.png" alt="">
          </div>
          <div class="link-text">
            <p>您好,{{user.FamilyName}}医生</p>
            <p>欢迎加入LCCP项目</p>
            <p>查看介绍,快速了解功能</p>
          </div>
        </div>
        <div class="function-link clearfix"
          v-show="user.EnrollDays < 28 && !user.IsPocketDoctor && !showSearch && currentFilter === 'default'">
          <a href="/lccp/mylccp/dxyintroduction?from=dxycard">
            <div class="link-pic">
              <img src="../../../assets/HomeView/dx_left.png" alt="">
            </div>
            <div class="link-text">
              <p>跟患者沟通需要使用丁香关爱</p>
              <p>注册丁香关爱入口,</p>
              <p>为什么需要注册丁香关爱?</p>
            </div>
          </a>
        </div>
      </div>
      <div class="no-patient-wrp">
        <img class="no-patient-img" v-show="currentFilter === 'default' && user.EnrollDays >= 28 && !sortPatients.length && !showSearch" src="../../../assets/HomeView/no_default.png" alt="">
        <img class="no-patient-img" v-show="currentFilter === 'msgRecords' && !sortPatients.length && !showSearch" src="../../../assets/HomeView/no_msg.png" alt="">
        <img class="no-patient-img" v-show="(currentFilter === 'noTest' || currentFilter === 'All') && !sortPatients.length && !showSearch" src="../../../assets/HomeView/no_enroll.png" alt="">
      </div>
      <div class="no-search" v-show="showSearch && searchValue && !sortPatients.length">
        <img src="../../../assets/HomeView/noResult.png" >
        <p class="no-search-title">未搜索到患者</p>
        <p>请更换搜索条件后重新尝试</p>
      </div>
    </section>
    <section class="send-msg" v-show="showChoose">
      <div class="choose-btn"
        @click="chooseAllPatient"
        :class="choosePatients.length && choosePatients.length === sortPatients.length ? 'choose-btn-active' : ''">
      </div>
      <span>{{choosePatients.length && choosePatients.length === sortPatients.length ? '取消全选' : '全选'}}</span>
      <p class="send-btn" :style="{background: choosePatients.length ? '' : '#9b9b9b'}" @click="sendMsg">发送血糖测量提醒</p>
    </section>
    <Footer v-show="!showChoose" />
    <Loading :showLoading="showLoading" />
    <Modal v-if="showPop" :title="'说明'" :showPop="showPop" :close="togglePop">
      <div class="intro">
        <p class="img-wrp"><img src="../../../assets/Setting/brand_logo.png" alt=""></p>
        <p>
          借助智能的血糖仪设备、便携的微信平台、专业的关爱中心支持帮助医生高效管理患者，
          提升诊疗效率。帮助患者养成自我监测血糖的习惯，提高患者的依从性。
        </p>
        <p class="img-wrp"><img src="../../../assets/Setting/introduce.png" alt=""></p>
        <div>
          为礼来优泌乐系列胰岛素用户 <br>
          打造糖尿病管理综合解决方案
        </div>
      </div>
    </Modal>
    <ConfirmToMsgModal id="ConfirmToMsgModal" :showConfirm="showConfirmToMsg" :patient="msgPatient" :close="closeConfirmToMsg" />
    <ConfirmModal :closeModal="closeModal" :showConfirm="showConfirm" :patientIds="choosePatients" :sendInfo="sendInfo" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import lowIcon from '../../../assets/HomeView/low.png'
import low3Icon from '../../../assets/HomeView/low3.png'
import highIcon from '../../../assets/HomeView/high.png'
import Footer from '../../Shared/Footer/Footer'
import Loading from '../../Shared/Loading/Loading'
import Modal from '../../Shared/Modal/Modal'
import ConfirmModal from '../../Shared/ConfirmModal/ConfirmModal'
import ConfirmToMsgModal from '../../Shared/ConfirmToMsgModal/ConfirmToMsgModal'

export default {
  name: 'patientManage',
  components: {
    Footer,
    Loading,
    ConfirmModal,
    ConfirmToMsgModal,
    Modal
  },
  data () {
    return {
      swiperOption: {
        autoplay: true,
        loop: true,
        speed: 500
      },
      sortPatients: [],
      msgPatient: '',
      timer: null,
      showChoose: false,
      showPop: false,
      isChangeLike: false,
      choosePatients: [],
      lowIcon: lowIcon,
      low3Icon: low3Icon,
      highIcon: highIcon,
      sendInfo: {
        text: '提醒测量'
      },
      currentFilter: 'default',
      currentRule: '',
      currentFilterSecond: '最后一次测血糖时间从新到旧',
      currentIndex: 0,
      showSearch: false,
      showSecondFilter: false,
      showLoading: false,
      searchValue: '',
      showConfirm: false,
      showConfirmToMsg: false,
      filters: [
        {
          rule: 'default',
          text: '重点关注'
        },
        {
          rule: 'msgRecords',
          text: '对话记录'
        },
        {
          rule: 'noTest',
          text: '新入组未测'
        },
        {
          rule: 'All',
          text: '全部患者',
          filterSecond: [
            {
              rule: 'lowCount',
              text: '近7天低血糖次数从多到少'
            },
            {
              rule: 'testCount',
              text: '近7天测量次数从多到少'
            },
            {
              rule: 'enrollDate',
              text: '入组时间从新到旧'
            },
            {
              rule: 'LastBgDataDate',
              text: '最后一次测血糖时间从新到旧'
            }
          ]
        }
      ]
    }
  },
  computed: {
    ...mapGetters({
      patients: 'patients',
      news: 'news',
      username: 'username',
      user: 'user',
      defaultPatients: 'defaultPatients',
      messagePatients: 'messagePatients',
      newPatients: 'newPatients',
      patientSendStatus: 'patientSendStatus',
      haveNewMessage: 'haveNewMessage'
    })
  },
  methods: {
    sortList (a, b) {
      let result = b[this.currentRule] - a[this.currentRule]
      if (result !== 0) {
        return result
      } else {
        return b.enrollDate - a.enrollDate
      }
    },
    showChooseBtn () {
      if (!this.showChoose) {
        const patients = this.sortPatients.filter(item => {
          return !item.IsInteraction && !(!item.testCount && item.enrollBeforeWeek)
        })
        if (!patients.length) {
          this.$toast('当前没有可发送人员', 'center')
          return
        } else {
          this.sortPatients = patients
        }
      } else {
        if (this.currentFilter === 'noTest') {
          this.sortPatients = this.newPatients
        } else {
          this.sortPatients = this.patients.sort(this.sortList)
        }
        this.choosePatients = []
      }
      this.showChoose = !this.showChoose
    },
    choosePatient (id) {
      const index = _.indexOf(this.choosePatients, id)
      if (index >= 0) {
        this.choosePatients.splice(index, 1)
      } else {
        this.choosePatients.push(id)
      }
    },
    chooseAllPatient () {
      if (this.choosePatients.length !== this.sortPatients.length) {
        this.sortPatients.forEach(item => {
          if (this.choosePatients.indexOf(item.PatientId) < 0) {
            this.choosePatients.push(item.PatientId)
          }
        })
      } else {
        this.choosePatients = []
      }
    },
    sendMsg () {
      if (this.choosePatients.length) {
        this.showConfirm = true
        this.showChoose = false
      }
    },
    handleShowFilters () {
      this.showSecondFilter = !this.showSecondFilter
    },
    goToDetail (patient) {
      this.$store.dispatch('fetchPatientItem', patient)
      if (this.showSearch) {
        this.$router.push('/patient-detail/' + patient.PatientId + '?from=search')
      } else {
        this.$router.push('/patient-detail/' + patient.PatientId + '?from=' + this.currentFilter)
      }
    },
    goToUrl (url) {
      if (!url) {
        return
      }
      const host = window.location.host
      const hostReg = new RegExp('' + host + '')
      const index = url.indexOf('/Plugins/Innocellence.Lccp.Wechat/VueDist/Index.html#/')
      this.$store.dispatch('pageClickTracking', {
        logType: 'click',
        message: 'click banner, jump to: ' + url
      })
      if (!hostReg.test(url) || (index === -1)) {
        location.href = url
      } else {
        const urlStrs = url.split('/Plugins/Innocellence.Lccp.Wechat/VueDist/Index.html#/')
        // const urlStrs = url.split('#/')
        // const reg = /.com/
        // const url = urlStrs[1].replace(reg, '')
        this.$router.push(`/${urlStrs[1]}`)
      }
    },
    goToPocket (patient) {
      this.msgPatient = patient
      this.showConfirmToMsg = true
    },
    autoPlay () {
      if (this.currentIndex > this.news.length) {
        this.currentIndex = 0
      }
      this.currentIndex++
    },
    showtags () {
      this.showSearch = !this.showSearch
      if (!this.showSearch) {
        this.searchValue = ''
        this.changeFilter(this.$route.params.id)
      } else {
        this.sortPatients = []
      }
    },
    search () {
      if (!this.searchValue.trim()) {
        this.sortPatients = []
        return
      }
      let reg = new RegExp(this.searchValue.trim())
      this.sortPatients = this.patients.filter((patient) => {
        return reg.test(patient.Name) || reg.test(patient.PatientCardNumber)
      })
    },
    filterByTag (index) {
      this.tags[index].choose = !this.tags[index].choose
      // const chooseTags = this.tags.filter(tag => {
      //   return tag.choose
      // })
    },
    changeFilter (filter, secondFilter) {
      if (!filter) {
        this.showSecondFilter = false
        this.currentRule = secondFilter.rule
        this.currentFilterSecond = secondFilter.text
        this.sortPatients = this.patients.sort(this.sortList)
      } else {
        this.currentFilter = filter
        this.$router.push('/patient-list/' + filter + '')
        this.showChoose = false
        this.choosePatients = []
        if (filter === 'default') {
          if (this.$refs.filters) {
            this.$refs.filters.scrollLeft = 0
          }
          this.sortPatients = this.defaultPatients
        } else if (filter === 'msgRecords') {
          if (this.$refs.filters) {
            this.$refs.filters.scrollLeft = 0
          }
          this.sortPatients = this.messagePatients
        } else if (filter === 'noTest') {
          if (this.$refs.filters) {
            this.$refs.filters.scrollLeft = 0
          }
          this.currentRule = 'enrollDate'
          this.sortPatients = this.newPatients.sort(this.sortList)
        } else if (filter === 'All') {
          if (this.$refs.filters) {
            this.$refs.filters.scrollLeft = 80
          }
          if (!this.patients.length) {
            this.sortPatients = []
          } else {
            this.currentRule = 'LastBgDataDate'
            this.sortPatients = this.patients.sort(this.sortList)
          }
        } else {
          this.sortPatients = this.patients.sort(this.sortList)
        }
      }
    },
    handleLike (patient) {
      this.isChangeLike = true
      var isLike = patient.IsStar ? '1' : '0'
      this.$store.dispatch('handleLike', {patientId: patient.PatientId, isLike: isLike, from: this.currentFilter})
      const msg = patient.IsStar ? '已将' + patient.Name + '从重点关注中移除' : '已将' + patient.Name + '加入重点关注'
      this.$toast(msg, 'center')
      setTimeout(() => {
        this.isChangeLike = false
      }, 600)
    },
    closeModal () {
      this.showConfirm = false
      this.showChoose = false
      this.choosePatients = []
    },
    closeConfirmToMsg () {
      this.showConfirmToMsg = false
    },
    togglePop () {
      this.showPop = !this.showPop
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: 'LCCP Introduction'})
    }
  },
  watch: {
    patients: function (val, oldVal) {
      if (val.length !== oldVal.length || (!val.length && this.patientSendStatus === 1)) {
        if (this.$route.params.id && (this.$route.params.id !== 'default')) {
          this.changeFilter(this.$route.params.id)
        } else {
          if (this.defaultPatients.length || !val.length) {
            this.sortPatients = this.defaultPatients
          } else {
            this.changeFilter('All')
          }
        }
        this.showLoading = false
      }
    },
    defaultPatients: function (val, oldVal) {
      if (this.currentFilter === 'default') {
        this.sortPatients = val
      }
    },
    news: function (val, oldVal) {
      if (val.length > 1) {
        this.timer = setInterval(() => {
          this.autoPlay()
        }, 3000)
      }
    },
    searchValue: function (val, oldVal) {
      if (this.showSearch) {
        this.search()
        this.$store.dispatch('pageClickTracking', {logType: 'input', message: 'Search: ' + val})
      }
    },
    $route: function (val, oldVal) {
      if (this.currentFilter !== val.params.id) {
        this.changeFilter(val.params.id)
      }
    }
  },
  beforeMount () {
    if (this.showConfirmToMsg) {
      this.showConfirmToMsg = false
    }
    if (!this.news.length) {
      this.$store.dispatch('fetchNews')
    }
    if (!this.patients.length && !this.patientSendStatus) {
      this.showLoading = true
      this.$store.dispatch('fetchPatientList')
    }
    if (!this.defaultPatients.length && this.patients.length && this.patientSendStatus) {
      this.changeFilter('All')
    }
    if (this.$route.params.id) {
      this.changeFilter(this.$route.params.id)
    }
    // fix: ios10口袋医生后退不刷新，仍显示中转页
    window.addEventListener('pageshow', function (e) {
      const confirmToMsgModal = document.getElementById('ConfirmToMsgModal')
      if (confirmToMsgModal) {
        location.reload(true)
      }
    })
  },
  mounted () {
    this.changeFilter(this.$route.params.id)
    if (this.news.length > 1) {
      this.timer = setInterval(() => {
        this.autoPlay()
      }, 3000)
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



