import httpClient from '../common/httpClient'
import { handlePatientList } from '../common/util'
// initial state
const state = {
  patients: [],
  doctorPatients: [],
  patientItem: {},
  news: [],
  status: 0,
  calendarSendStatus: 0,
  lineSendStatus: 0,
  departWatchDoctor: {},
  sendMsgCode: '',
  chatUrl: ''
}

// getters
const getters = {
  patients: state => state.patients,
  doctorPatients: state => state.doctorPatients,
  defaultPatients: state => {
    const likePatients = state.patients.filter(item => {
      return item.IsStar
    }).sort((a, b) => {
      let result = b.hasError - a.hasError
      let result1 = b.IsNew - a.IsNew
      let result2 = b.LastBgDataDate - a.LastBgDataDate
      return result === 0
        ? (result1 === 0
          ? (result2 === 0 ? b.enrollDate - a.enrollDate : result2) : result1) : result
    })
    const errorlPatients = state.patients.filter(item => {
      return item.hasError && !item.IsStar
    }).sort((a, b) => {
      let result = b.hasError - a.hasError
      let result1 = b.LastBgDataDate - a.LastBgDataDate
      return result === 0 ? (result1 === 0 ? b.enrollDate - a.enrollDate : result1) : result
    })
    const newPatients = state.patients.filter(item => {
      return item.IsNew && !item.IsStar && !item.hasError
    }).sort((a, b) => {
      let result = b.LastBgDataDate - a.LastBgDataDate
      let result1 = b.enrollDate - a.enrollDate
      return result === 0 ? result1 : result
    })
    return [...likePatients, ...errorlPatients, ...newPatients]
  },
  newPatients: state => {
    const result = _.groupBy(state.patients, 'IsNew')
    return result['true'] || []
  },
  messagePatients: state => {
    const messagePatients = state.patients.filter(item => {
      return item.KouDai_MessageTime
    }).sort((a, b) => {
      return b.KouDai_MessageTime - a.KouDai_MessageTime
    })
    return messagePatients
  },
  haveNewMessage: state => {
    const unReadPatients = state.patients.filter(item => {
      return item.KouDai_MessageTime && !item.KouDai_IsHcpRead && item.KouDai_From === 'patient'
    })
    return unReadPatients.length > 0
  },
  patientItem: state => state.patientItem,
  news: state => state.news,
  sendMsgCode: state => state.sendMsgCode,
  patientSendStatus: state => state.status,
  departWatchDoctor: state => state.departWatchDoctor,
  calendarSendStatus: state => state.calendarSendStatus,
  lineSendStatus: state => state.lineSendStatus,
  chatUrl: state => state.chatUrl
}

// actions
const actions = {
  handleLike ({ commit }, patient) {
    httpClient.post('/lccp/PatientList/SetStar?like=' + patient.isLike + '&from=' + patient.from,
      {pid: patient.patientId}).then(() => {
      commit('changeLike', patient.patientId)
    })
  },
  sendMsg ({commit}, data) {
    httpClient.post('/lccp/DoctorPatientInteraction/DoctorSentMessageToPatient', data).then(response => {
      commit('updateMsgCode', response.returnCode)
      if (response.returnCode === 200) {
        commit('sendMsg', data.patientId)
      }
    })
  },
  batchSendMsg ({commit}, patient) {
    httpClient.post('/lccp/DoctorPatientInteraction/BatchDoctorSentMessageToPatient?from=' + patient.from,
      {patientIdList: JSON.stringify(patient.patientIds)}).then(response => {
      commit('updateMsgCode', response.returnCode)
      if (response.returnCode === 200) {
        commit('sendMsg', patient.patientIds)
      }
    })
  },
  resetSendMsgCode ({commit}) {
    commit('updateMsgCode', '')
  },
  departWatchDoctor ({commit}, doctor) {
    commit('departWatchDoctor', doctor)
  },
  fetchNews ({commit}) {
    httpClient.get('/lccp/Banners/GetBanners/').then(response => {
      commit('updateNews', response.data)
    })
  },
  async fetchPatientList ({commit}) {
    commit('updatePatientStatus', 0)
    await httpClient.get('/lccp/PatientList/GetCardsList/').then(response => {
      commit('fetchPatientList', response.data)
    })
  },
  async fetchPatientByDoctor ({commit}, HcpId) {
    commit('updatePatientStatus', 0)
    await httpClient.post('/lccp/PatientList/GetDoctorCardsList', {doctorHcpId: HcpId}).then(response => {
      commit('fetchPatientByDoctor', response.data.PatientCardList)
      commit('departWatchDoctor', {DoctorName: response.data.DoctorName})
    })
  },
  async fetchPatientByShare ({commit}) {
    commit('updatePatientStatus', 0)
    await httpClient.post('/lccp/patientlist/GetPatientDetailFromQRCode').then(response => {
      commit('fetchPatientByShare', response.data)
      // commit('departWatchDoctor', {DoctorName: response.data.DoctorName})
    })
  },
  async fetchPatientItem ({dispatch, commit, state}, data) {
    if (data.PatientId) {
      commit('fetchPatientItem', data)
    } else if (data.hcpId) {
      const patientId = data.patientId
      await dispatch('fetchPatientByDoctor', data.hcpId)
      for (let i = 0; i < state.doctorPatients.length; i++) {
        if (state.doctorPatients[i].PatientId === parseInt(patientId)) {
          commit('fetchPatientItem', state.doctorPatients[i])
          return
        }
      }
    } else {
      const patientId = data
      await dispatch('fetchPatientList')
      for (let i = 0; i < state.patients.length; i++) {
        if (state.patients[i].PatientId === parseInt(patientId)) {
          commit('fetchPatientItem', state.patients[i])
          return
        }
      }
    }
  },
  fetchDetailData ({commit}, data) {
    commit('updateDetailStatus', data.tab)
    httpClient.post('/lccp/PatientDetails/GetPatientBGData/', data).then(response => {
      if (data.tab) {
        commit('fetchHalfYearData', response.data)
      } else {
        commit('fetchMonthData', response.data)
      }
    })
  },
  fetchDetailShareData ({commit}, data) {
    commit('updateDetailStatus', data.tab)
    httpClient.post('/lccp/PatientDetails/GetPatientBGDataShare/', data).then(response => {
      if (data.tab) {
        commit('fetchHalfYearData', response.data)
      } else {
        commit('fetchMonthData', response.data)
      }
    })
  },
  goToPocket ({ commit }, patient) {
    httpClient.post(`/lccp/PocketDoctor/GetPocketDoctorConversationPath?from=${patient.KouDai_From}`,
      {patientId: patient.PatientId}).then(response => {
      httpClient.post('/lccp/PatientList/ClearUnReadStatus', {pid: patient.PatientId})
      commit('clearUnReadStatus', patient.PatientId)
      commit('updateChatUrl', response.data)
    })
  },
  resetDetailStatus ({commit}) {
    commit('resetDetailStatus')
    commit('updateMsgCode', '')
    commit('updateChatUrl', '')
  }
}

// mutations
const mutations = {
  changeLike (state, id) {
    state.patients.forEach((item) => {
      if (item.PatientId === id) {
        item.IsStar = !item.IsStar
      }
    })
  },
  updateNews (state, data) {
    state.news = data
  },
  updatePatientStatus (state, status) {
    state.status = status
  },
  updateChatUrl (state, url) {
    state.chatUrl = url
  },
  departWatchDoctor (state, doctor) {
    state.departWatchDoctor = doctor
  },
  updateDetailStatus (state, tab) {
    if (tab) {
      state.lineSendStatus = 0
    } else {
      state.calendarSendStatus = 0
    }
  },
  resetDetailStatus (state) {
    state.lineSendStatus = 0
    state.calendarSendStatus = 0
  },
  sendMsg (state, id) {
    if (id.length !== undefined) {
      for (let i = 0; i < state.patients.length; i++) {
        if (id.indexOf(state.patients[i].PatientId) > -1) {
          state.patients[i].IsInteraction = true
        }
      }
    } else {
      state.patientItem.IsInteraction = true
      for (let i = 0; i < state.patients.length; i++) {
        if (state.patients[i].PatientId === id) {
          state.patients[i].IsInteraction = true
        }
      }
    }
  },
  updateMsgCode (state, status) {
    state.sendMsgCode = status
  },
  fetchPatientList (state, data) {
    state.patients = handlePatientList(data)
    state.status = 1
  },
  fetchPatientByDoctor (state, data) {
    state.doctorPatients = handlePatientList(data)
    state.status = 1
  },
  fetchPatientByShare (state, data) {
    const patientItem = handlePatientList([data.Patient])[0]
    patientItem.ShowQRCode = data.ShowQRCode
    state.patientItem = patientItem
    state.status = 1
  },
  fetchPatientItem (state, data) {
    state.patientItem = data
  },
  fetchMonthData (sate, data) {
    data.forEach(data => {
      data.color = data.Value <= 3.9 ? 'red'
        : (data.Value >= 10 || (data.Value >= 7 && data.TimeRange === 0) ? '#4D92E3' : '#A0A0A0')
      data.Value = Math.round(data.Value * 10) / 10
      const valueStr = data.Value.toString()
      const strIndex = valueStr.indexOf('.')
      if (strIndex < 0) {
        data.Value += '.0'
      }
    })
    Object.assign(state.patientItem.calendarData, _.groupBy(data, 'Day'))
    state.calendarSendStatus = 1
  },
  fetchHalfYearData (state, data) {
    state.patientItem.maxValue = data.length ? _.maxBy(data, 'Value').Value : 0
    state.patientItem.lineData = data
    state.lineSendStatus = 1
  },
  clearUnReadStatus (state, patientId) {
    for (let i = 0; i < state.patients.length; i++) {
      if (state.patients[i].PatientId === parseInt(patientId)) {
        state.patients[i].KouDai_IsHcpRead = true
        return
      }
    }
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



