<template>
  <div class="modal" :class="opaque ? 'no-opacity' : ''" v-show="showPop" @click="close">
    <div
      :class="title !== '医生端介绍' && title !== '患者端介绍'
        ? 'modal-content' : 'modal-content-swiper'"
      @click.stop="prevent"
      :style="{height: autoHeight ? '' : '85%'}">
      <div class="header clearfix" v-if="title !== '医生端介绍' && title !== '患者端介绍'">
        <span class="title">{{title}}</span>
        <span class="close-button" @click="close"><img src="../../../assets/close.png"></span>
      </div>
      <div class="shape" v-if="title === '优行™使用条款'"></div>
      <div class="content-wrp" ref="contentWrp" v-if="title !== '医生端介绍' && title !== '患者端介绍'">
        <slot></slot>
      </div>
      <div class="hcpIntro" v-show="title === '医生端介绍'">
        <swiper :options="swiperOption" ref="mySwiper">
          <!-- slides -->
          <swiper-slide v-for="(item, index) in hcpIntros" :key="index">
            <div class="header clearfix">
              <span class="title">{{item.header}}</span>
              <span class="close-button" @click="close"><img src="../../../assets/close.png"></span>
            </div>
            <div class="content-wrp">
              <h2>{{item.text}}</h2>
              <img :src="item.img" alt="">
            </div>
          </swiper-slide>
          <!-- Optional controls -->
          <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
      </div>
      <div class="patientIntro" v-show="title === '患者端介绍'">
        <swiper :options="swiperOption" ref="mySwiper1">
          <!-- slides -->
          <swiper-slide v-for="(item, index) in patientIntros" :key="index">
            <div class="header clearfix">
              <span class="title">{{item.header}}</span>
              <span class="close-button" @click="close"><img src="../../../assets/close.png"></span>
            </div>
            <div class="content-wrp">
              <h2>{{item.text}}<br>{{item.text1 ? item.text1 : ''}}</h2>
              <img :src="item.img" alt="">
            </div>
          </swiper-slide>
          <!-- Optional controls -->
          <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
      </div>
    </div>
  </div>
</template>
<script>
// import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import hcpImg1 from '../../../assets/Setting/hcpIntro1.png'
import hcpImg2 from '../../../assets/Setting/hcpIntro2.png'
import hcpImg3 from '../../../assets/Setting/hcpIntro3.png'
import hcpImg4 from '../../../assets/Setting/hcpIntro4.png'
import hcpImg5 from '../../../assets/Setting/hcpIntro5.png'
import hcpImg6 from '../../../assets/Setting/hcpIntro6.png'
import hcpImg7 from '../../../assets/Setting/hcpIntro7.png'
import patientImg0 from '../../../assets/Setting/patientIntro0.png'
import patientImg1 from '../../../assets/Setting/patientIntro1.png'
import patientImg2 from '../../../assets/Setting/patientIntro2.png'
import patientImg3 from '../../../assets/Setting/patientIntro3.png'
import patientImg4 from '../../../assets/Setting/patientIntro4.png'
export default {
  props: ['title', 'showPop', 'close', 'opaque', 'autoHeight'],
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      swiperOption: {
        // autoplay: true,
        slidesPerView: 1.2,
        spaceBetween: 20,
        mousewheel: true,
        centeredSlides: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      },
      hcpIntros: [
        {
          header: '患者列表',
          text: '智能分组，提升患者管理效率',
          img: hcpImg1
        },
        {
          header: '血糖日历',
          text: '患者近期血糖，一目了然',
          img: hcpImg2
        },
        {
          header: '异常血糖提醒',
          text: '及时定位血糖波动和高危患者',
          img: hcpImg3
        },
        {
          header: '治疗方案',
          text: '调节治疗方案有迹可循',
          img: hcpImg4
        },
        {
          header: '科室报告',
          text: '提升科室影响力',
          img: hcpImg5
        },
        {
          header: '点赞/提醒复诊',
          text: '干预患者行为，建立长期联系',
          img: hcpImg6
        },
        {
          header: '在线问诊',
          text: '强化患者干预',
          img: hcpImg7
        }
      ],
      patientIntros: [
        {
          header: '治疗方案',
          text: '患者可随时更新治疗方案',
          img: patientImg0
        },
        {
          header: '血糖日历',
          text: '血糖值变化，近期数据一目了然',
          img: patientImg1
        },
        {
          header: '积分社区',
          text: '积分兑换试纸套装',
          text1: '提高患者参与治疗积极性',
          img: patientImg2
        },
        {
          header: '家属入组',
          text: '家属绑定患者，及时知晓患者详情',
          img: patientImg3
        },
        {
          header: '糖知识问答',
          text: '患者在答题中深入了解糖尿病知识',
          img: patientImg4
        }
      ]
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    },
    swiper1 () {
      return this.$refs.mySwiper1.swiper
    }
  },
  watch: {
    showPop (val) {
      if (val) {
        document.body.setAttribute('class', 'locked')
      } else {
        document.body.removeAttribute('class')
      }
      this.swiper.slideTo(0, 0, false)
      this.swiper1.slideTo(0, 0, false)
      if (this.$refs.contentWrp && this.$refs.contentWrp.scrollTop) {
        this.$refs.contentWrp.scrollTop = 0
      }
    }
  },
  methods: {
    prevent () {
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



