import httpClient from '../common/httpClient'
// initial state
const state = {
  allDoctors: {
    0: {
      hasSend: false,
      doctors: []
    },
    1: {
      hasSend: false,
      doctors: []
    },
    2: {
      hasSend: false,
      doctors: []
    }
  },
  currentTime: 0,
  sendStatus: true
}

// getters
const getters = {
  doctors: state => {
    let doctors = state.allDoctors[state.currentTime].doctors
    if (doctors.length) {
      return doctors.sort(function (a, b) {
        let result = b['BGCount'] - a['BGCount']
        if (result !== 0) {
          return result
        } else {
          return a.DoctorName.localeCompare(b.DoctorName, 'zh-CN')
        }
      })
    } else {
      return doctors
    }
  },
  sendStatus: state => state.sendStatus
}

// actions
const actions = {
  fetchDoctorList ({ commit, state }, time) {
    commit('updateCurrentTime', time)
    if (!state.allDoctors[time].hasSend) {
      commit('changeSendStatus', true)
      httpClient.post('/lccp/DoctorList/GetDoctorList/', {time: time}).then((response) => {
        commit('fetchDoctorList', {doctors: response.data, time: time})
        commit('changeSendStatus', false)
      })
    }
  }
}

// mutations
const mutations = {
  fetchDoctorList (state, data) {
    state.allDoctors[data.time].doctors = data.doctors
    state.allDoctors[data.time].hasSend = true
  },
  changeSendStatus (state, status) {
    state.sendStatus = status
  },
  updateCurrentTime (state, time) {
    state.currentTime = time
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



