<template>
  <div>
    <ul class="footer" v-if="$route.name !== 'srSummary'">
      <router-link tag="li" :to="link.url" class="footer-item"
        v-for="(link,index) in links" :key="index" exact>
        <img :src="link.img" class="link-icon" alt="">
        <img :src="link.activeImg" class="link-icon link-icon-active" alt="">
      </router-link>
    </ul>
  </div>
</template>
<script>
import footer1 from '@/assets/Footer/footer_1.png'
import footer11 from '@/assets/Footer/footer_1_1.png'
import footer2 from '@/assets/Footer/footer_2.png'
import footer21 from '@/assets/Footer/footer_2_1.png'
import footer3 from '@/assets/Footer/footer_3.png'
import footer31 from '@/assets/Footer/footer_3_1.png'
import footer4 from '@/assets/Footer/footer_4.png'
import footer41 from '@/assets/Footer/footer_4_1.png'
export default {
  data () {
    return {
      links: [
        {
          text: '患者',
          url: '/patient-list/default',
          img: footer1,
          activeImg: footer11
        },
        {
          text: '概要',
          url: '/summary/sugarControl',
          img: footer2,
          activeImg: footer21
        },
        {
          text: '邀请',
          url: '/invitation',
          img: footer3,
          activeImg: footer31
        },
        {
          text: '设置',
          url: '/setting',
          img: footer4,
          activeImg: footer41
        }
      ]
    }
  },
  computed: {
    user () {
      return this.$store.getters.user
    }
  },
  watch: {
    $route: function (val, oldVal) {
      if (val.name === 'patientManage') {
        this.links[0].url = val.path
      }
      if (val.name === 'Summary') {
        this.links[1].url = val.path
      }
    }
  },
  beforeMount () {
    if (this.$route.name === 'patientManage') {
      this.links[0].url = this.$route.path
    }
    if (this.user.IsReport) {
      this.links[1].url = '/summary/DepartControl'
    }
    if (this.$route.name === 'Summary') {
      this.links[1].url = this.$route.path
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



