<template>
  <div class="confirm-page" v-if="showConfirm">
    <div class="confirm" v-if="!user.IsPocketDoctorRegister && !sendSuccess">
      <div class="confirm-img">
        <img :src="dxImg">
      </div>
      <p class="confirm-msg">即将跳转第三方"丁香关爱"会话页面</p>
      <p class="confirm-btns">
        <span class="btn-ensure"
          @click="chatWithPatient">确定, 以后不再提醒</span>
      </p>
    </div>
    <div class="tansfer-wrp" v-show="sendSuccess">
      <img :src="tansferImg">
    </div>
  </div>
</template>
<script>
import dxImg from '../../../assets/Confirm/dxImg.png'
import tansferImg from '../../../assets/Confirm/tansfer.jpg'
export default {
  props: ['showConfirm', 'patient', 'close'],
  data () {
    return {
      dxImg: dxImg,
      tansferImg: tansferImg,
      sendSuccess: false,
      hasClick: false
    }
  },
  computed: {
    user () {
      return this.$store.getters.user
    },
    chatUrl () {
      return this.$store.getters.chatUrl
    }
  },
  methods: {
    chatWithPatient () {
      if (this.hasClick) {
        return
      }
      if (!this.user.IsPocketDoctorRegister) {
        this.$store.dispatch('setPocket')
      }
      this.$store.dispatch('goToPocket', this.patient)
      this.hasClick = true
    }
  },
  watch: {
    chatUrl: function (val, oldVal) {
      if (val) {
        this.sendSuccess = true
        setTimeout(() => {
          window.location.href = val
        }, 2000)
      }
    },
    showConfirm: function (val, oldVal) {
      if (val && this.user.IsPocketDoctorRegister) {
        this.sendSuccess = true
        this.chatWithPatient()
      }
      if (!val) {
        this.sendSuccess = false
        this.hasClick = false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



