import photo from '@/assets/HomeView/person.svg'
export const handlePatientList = function (data) {
  let arr = data
  const now = new Date()
  const scheme = {
    1: '每天一针（睡前或晚餐前注射）',
    2: '每天两针（早晚餐前30分钟注射）',
    3: '每天两针（早晚餐前5分钟注射）',
    4: '每天三针（三餐前注射）',
    5: '每天四针（三餐及睡前注射）',
    6: '胰岛素泵',
    7: '每天两针'
  }
  const daySecond = 24 * 60 * 60
  const weekSecond = 7 * daySecond
  const yearSecond = 365 * daySecond
  const twelveWeekSecond = 12 * weekSecond
  arr.map((item, index) => {
    const timeStamp = parseInt(item.PatientEnrollmentDate + '000')
    const enrollDate = new Date(timeStamp)
    const enrollTime = (now - timeStamp) / 1000
    let lowCount = 0
    let highCount = []
    item.tags = []
    item.enrollDate = enrollDate
    const schemeObj = scheme[item.Te_Scheme] ? scheme[item.Te_Scheme].split('（') : []
    item.Te_Scheme0 = scheme[item.Te_Scheme] ? scheme[item.Te_Scheme] : ''
    item.Te_Scheme = schemeObj[0] ? schemeObj[0] : ''
    item.Te_Scheme1 = schemeObj[1] ? schemeObj[1].split('）')[0] : ''
    item.sex = item.Gender === '1' ? '男' : '女'
    item.enrollBeforeWeek = enrollTime < weekSecond
    const tempDate = new Date(item.Te_Updated * 1000)
    item.Te_Updated_Date = tempDate.getFullYear() + '年' + (tempDate.getMonth() + 1) + '月' + tempDate.getDate() + '日'
    item.Name = item.Name.length && item.Name.length > 9 ? item.Name.slice(0, 9) + '...' : item.Name
    item.enrollDaysNum = Math.ceil(enrollTime / daySecond)
    item.enrollDays = enrollTime < (daySecond * 2) ? '24小时前入组' : (enrollTime < weekSecond ? parseInt(enrollTime / daySecond) + '天前入组'
      : (enrollTime < twelveWeekSecond ? parseInt(enrollTime / weekSecond) + '周前入组'
        : (enrollTime < yearSecond
          ? (now.getMonth() - enrollDate.getMonth() <= 0 ? 12 + (now.getMonth() - enrollDate.getMonth())
            : now.getMonth() - enrollDate.getMonth()) + '个月前入组'
          : '1年前入组')))
    if (item.KouDai_MessageTime) {
      item.KouDai_MessageTime = new Date(parseInt(item.KouDai_MessageTime.slice(6, item.KouDai_MessageTime.length - 2)))
      const tempTime = item.KouDai_MessageTime
      item.KouDai_MessageDate = now.getFullYear() > tempTime.getFullYear() ? '1年前'
        : (now.getMonth() > tempTime.getMonth() || now.getDate() - tempTime.getDate() > 1 ? tempTime.getMonth() + 1 + '月' + tempTime.getDate() + '日'
          : (now.getDate() === tempTime.getDate() ? `${tempTime.getHours() < 10 ? '0' + tempTime.getHours() : tempTime.getHours()} : ${tempTime.getMinutes() < 10 ? '0' + tempTime.getMinutes() : tempTime.getMinutes()}` : '昨天'))
    }
    item.photo = item.Avatar ? item.Avatar : photo
    item.BMI = 0
    if (item.Te_Height && item.Te_Weight) {
      item.BMI = parseInt(item.Te_Weight / (item.Te_Height * item.Te_Height / 10000) * 10) / 10
    }
    const birthday = new Date(parseInt(item.Birthday + '000'))
    item.age = now.getFullYear() - birthday.getFullYear() - 1
    if (now.getMonth() > birthday.getMonth() || (birthday.getMonth() === now.getMonth() && now.getDate() >= birthday.getDate())) {
      item.age++
    }
    item.LastBgDataDate = item.LastBgDataDate ? parseInt(item.LastBgDataDate.slice(6, item.LastBgDataDate.length - 2)) : ''
    item.testCount = item.BGData.length
    item.BGData.forEach((data, index) => {
      if (data.Value >= 13.9) {
        highCount.push(index)
        if (data.Value >= 16.7) {
          data.high = true
          if (item.tags.indexOf('高血糖') < 0) {
            item.tags.push('高血糖')
          }
        }
      } else if (data.Value <= 3.9) {
        lowCount++
        data.low = true
        if (data.Value <= 3.0) {
          data.veryLow = true
          if (item.tags.indexOf('极低血糖') < 0) {
            item.tags.push('极低血糖')
          }
        }
      }
      data.color = data.Value <= 3.9 ? 'red'
        : (data.Value >= 10 || (data.Value >= 7 && data.TimeRange === 0) ? '#4D92E3' : '#A0A0A0')
      data.Value = Math.round(data.Value * 10) / 10
      const valueStr = data.Value.toString()
      const strIndex = valueStr.indexOf('.')
      if (strIndex < 0) {
        data.Value += '.0'
      }
    })
    if (lowCount >= 3 || (item.BGData.length <= 5 && item.tags.indexOf('连续低血糖') < 0 && lowCount && lowCount >= (item.BGData.length / 2))) {
      item.tags.push('连续低血糖')
    }
    if (highCount.length >= 3 || (item.BGData.length <= 5 && highCount.length && highCount.length >= (item.BGData.length / 2))) {
      const index = item.tags.indexOf('高血糖')
      if (index < 0) {
        item.tags.push('高血糖')
      }
      highCount.forEach(i => {
        item.BGData[i].high = true
      })
    }
    const highIndex = item.tags.indexOf('高血糖')
    if (highIndex > -1) {
      item.tags.splice(highIndex, 1)
      item.tags.push('高血糖')
    }
    item.calendarData = _.groupBy(item.BGData, 'Day')
    item.hasError = item.tags.length ? 1 : 0
    item.lowCount = lowCount
    return item
  })
  return arr
}



