import httpClient from '../common/httpClient'

// initial state
const state = {
  settingStatus: [],
  settings: [
    {
      value: 'IsPushPatientBgWarning',
      text: '患者血糖异常的微信通知',
      map: 'V4',
      status: ''
    },
    {
      value: 'IsPushWhenNewCardJoin',
      text: '新卡片绑定后的微信通知',
      map: 'V2',
      status: ''
    }
  ]
}

// mutations
const mutations = {
  fetchSettingList (state, data) {
    state.settingStatus = [
      data.IsPushPatientBgWarning,
      data.IsPushWhenNewCardJoin
    ]
    state.settings.forEach((item, index) => {
      item.status = state.settingStatus[index] ? 'on' : ''
    })
  },
  updateSetting (state, data) {
    state.settings = data
  }
}

const getters = {
  settingStatus: state => state.settingStatus,
  settings: state => state.settings
}

// actions
const actions = {
  fetchSettingList ({ commit }) {
    httpClient.get('/lccp/PushSetting/GetPushSetting/').then(response => {
      commit('fetchSettingList', response.data)
    })
  },
  setPushSetting ({ commit }, data) {
    const result = {result: JSON.stringify(data.result)}
    httpClient.post('/lccp/PushSetting/SetPushSetting/', result).then(response => {
      commit('updateSetting', data.settings)
    })
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



